/* eslint-disable */
<template>
  <base-template>
    <h1 class="visually-hidden">Простая кухня</h1>
    <home-menu />
  </base-template>
</template>

<script>
import axios from "axios";
import BaseTemplate from "@/templates/BaseTemplate";
import HomeMenu from "@/components/blocks/HomeMenu";
export default {
  name: "HomePage",
  components: {
    BaseTemplate,
    HomeMenu,
  },
  data() {
    return {};
  },
  methods: {
    async fetchData() {
      const sessid = this.$store.getters["base/SESSID"];

      try {
        const response = await axios.get("get.mainpage", {
          params: { sessid },
        });
        console.log(response.data.result);
      } catch (error) {
        console.log(error);
      }
    },
  },
  async mounted() {
    await this.fetchData();
  },
};
</script>
