import axios from "axios";
import VueCookies from "vue-cookies";
import Vue from "vue";
const baseUrl = process.env.VUE_APP_API_BASE_URL;

axios.defaults.baseURL = baseUrl;

Vue.use(VueCookies);

export default {
  namespaced: true,
  state: {
    data: [],
    sessid: "",
    usersessid: "",
    menu: [],
    socials: [],
    footer: {},
    footerMenu: {},
    linkLibrary: {},
    user: [],
  },
  mutations: {
    SET_BASE: (state, data) => {
      state.data = data;
      state.sessid = data.session_id;
      state.footer = data.footer_info;
      state.footerMenu = data.footer_menu;
    },
  },
  actions: {
    async fetch({ commit }) {
      try {
        let getParams = "";
        if (Vue.$cookies.isKey("PHPSESSID")) {
          getParams = "/?sessid=" + window.$cookies.get("PHPSESSID");
        }

        const { data } = await axios.get("get.basic" + getParams);
        commit("SET_BASE", data.result);
      } catch (error) {
        console.log(error);
        return error;
      }
    },
  },
  getters: {
    MENU: (state) => {
      return state.menu;
    },
    SOCIALS: (state) => {
      return state.socials;
    },
    FOOTER_MENU: (state) => {
      return state.footerMenu;
    },
    FOOTER: (state) => {
      return state.footer;
    },
    LINK_LIBRARY: (state) => {
      return state.linkLibrary;
    },
    SESSID: (state) => {
      return state.sessid;
    },
    USERSESSID: (state) => {
      return state.usersessid;
    },
    USER: (state) => {
      return state.user;
    },
  },
};
