<template>
  <router-view />
</template>
<script>
import { mapActions, mapState } from "vuex";

import Vue from "vue";

export default {
  components: {},
  data() {
    return {
      isLoaded: false,
      isHealthcare: false,
      inCookies: false,
    };
  },
  computed: {
    ...mapState("base", {
      base: (state) => state.data,
    }),
  },
  methods: {
    ...mapActions({
      fetchBase: "base/fetch",
    }),
    getCookies() {
      const healthcareCookies = Vue.$cookies.get("healthcare");
      console.log("1", this.isHealthcare);
      if (
        typeof healthcareCookies !== "undefined" &&
        healthcareCookies !== null
      ) {
        console.log("2", this.isHealthcare);
        this.inCookies = true;
        this.isHealthcare = healthcareCookies;
        console.log("3", this.isHealthcare);
      } else {
        this.inCookies = false;
        console.log("4", this.isHealthcare);
      }
    },
    setCookies(data) {
      console.log("$emit");
      Vue.$cookies.set("healthcare", data);
      this.inCookies = true;
      this.isHealthcare = data;
    },
  },
  async mounted() {
    await this.fetchBase();
    this.isLoaded = true;
    //this.getCookies();
    console.log("5", this.isHealthcare);
  },
};
</script>
